.header-status {
  display: flex;
  justify-content: space-between;
  
  padding: 0 0 0 4px;
  margin: 0 0 26px;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.header-status__time {
  width: 30.76px;
  height: 12px;
}

.header-status__indicator {
  width: 73px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-status__signal {
  width: 17.96px;
  height: 11.29px;
}

.header-status__wifi {
  width: 16.14px;
  height: 11.61px;
}

.header-status__battery {
  width: 25.71px;
  height: 12px;
}