.features {
  padding: 25px 20px 131px;
  box-sizing: border-box;

  @media (min-width: $desktop-width) {
    width: 1400px;

    padding: 65px 0 0;
    margin: 0 auto;
  }
}

.features__title {
  font-size: 24px;
  line-height: 24px;
  font-weight: 600;
  letter-spacing: -1px;
  text-align: center;

  margin: 0 0 12px;

  @media (min-width: $desktop-width) {
    font-size: 48px;
    line-height: 64px;
  }
}

.features__description {
  letter-spacing: -2%;
  text-align: center;

  margin: 0 0 40px;

  @media (min-width: $desktop-width) {
    width: 542px;

    font-size: 16px;
    line-height: 30px;

    margin: 0 auto 48px;
  }
}

.features__description-dop-text {
  display: none;

  @media (min-width: $desktop-width) {
    display: inline;
  }
}

.features__list {
  list-style: none;

  display: flex;
  flex-flow: row wrap;

  padding: 0 5px 0 10px;
  margin: 0;

  @media (min-width: $desktop-width) {
    padding: 0;
    margin: 0 0 0 115px;
  }
}

.features__item {
  width: 100%;

  margin: 0 0 48px;

  @media (min-width: $desktop-width) {
    width: 320px;

    margin: 0 80px 0 0;
  }

  &:last-child {
    margin: 0;
  }
}

.features__affordable-pricing {
  width: 48px;
  height: 48px;

  @media (min-width: $desktop-width) {
    width: 72px;
    height: 72px;
  }
}

.features__seamless-reliability {
  width: 37.05px;
  height: 48px;

  @media (min-width: $desktop-width) {
    width: 55.58px;
    height: 72px;
  }
}

.features__secure-collaboration {
  width: 47.44px;
  height: 48px;

  @media (min-width: $desktop-width) {
    width: 71.15px;
    height: 72px;
  }
}

.features__item-title {
  font-size: 20px;
  line-height: 24.2px;
  font-weight: 600;

  margin: 15px 0 12px;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 29.05px;

    margin: 22px 0 14px;
  }
}

.features__item-description {
  line-height: 24px;

  margin: 0 0 20px;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 26px;
    
    margin: 0 0 30px;
  }
}

.features__item-more {
  font-size: 20px;
  line-height: 16px;
    
  margin: 0;
}