.about {
  width: 100%;

  padding: 59px 21px 25px 22px;
  box-sizing: border-box;

  @media (min-width: $desktop-width) {
    width: 1400px;

    display: flex;
    flex-flow: row wrap;

    padding: 60px 0 65.12px 116px;
    margin: 0 auto;
  }
}

.about__title {
  font-size: 20px;
  line-height: 30px;
  font-weight: 600;

  padding: 0 0 0 2px;
  margin: 0 0 24px;

  @media (min-width: $desktop-width) {
    width: 494px;

    font-size: 36px;
    line-height: 50px;

    padding: 0;
    margin: 0 277px 0 0;
  }
}

.about__list {
  width: 55%;

  list-style: none;

  display: flex;
  justify-content: space-between;

  padding: 0;
  margin: 0 auto 17px;

  @media (min-width: $desktop-width) {
    width: 337.28px;

    margin: 0;
  }
}

.about__item {
  width: 35.53%;
  min-width: 72.18px;

  margin: 0 75.82px 0 0;

  @media (min-width: $desktop-width) {
    width: 137px;

    margin: 0 auto 0 0;
  }

  &:nth-child(2) {
    width: 27.15%;
    min-width: 55.16px;

    @media (min-width: $desktop-width) {
      width: 107.28px;
  
      margin: 0;
    }
  }

  &:last-child {
    margin: 0;
  }
}

.about__result {
  font-size: 31px;
  text-align: left;

  display: block;

  margin: 0 0 13px;

  @media (min-width: $desktop-width) {
    font-size: 60px;
    line-height: 70px;
    font-weight: 700;
    letter-spacing: -1px;

    margin: 0 0 8px;
  }
}

.about__heart-container {
  width: 73.68%;

  display: flex;
  justify-content: space-between;

  @media (min-width: $desktop-width) {
    width: 110px;

    margin: 0 0 12px;
  }
}

.about__heart {
  width: 8.51px;
  height: 7.44px;

  @media (min-width: $desktop-width) {
    width: 16px;
    height: 14px;
  }
}

.about__star-container {
  width: 100%;

  display: flex;
  justify-content: space-between;

  @media (min-width: $desktop-width) {
    width: 107.27px;

    margin: 0 0 12.06px;
  }
}

.about__star {
  width: 8.58px;
  height: 8.17px;

  @media (min-width: $desktop-width) {
    width: 16.69px;
    height: 15.9px;
  }
}

.about__text {
  font-size: 8px;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 28px;
    font-weight: 400;
    
  }
}

.about__list-partners {
  list-style: none;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0 5.14px 0 0;
  margin: 0 0 1px;

  @media (min-width: $desktop-width) {
    width: 1110px;

    padding: 0;
    margin: 50px 0 0 30px;
  }
}

.about__item-partners {
  width: max-content;

  display: flex;

  &:nth-child(5) {
    display: none;

    @media (min-width: $desktop-width) {
      display: inherit;
    }
  }

  &:last-child {
    display: none;

    @media (min-width: $desktop-width) {
      display: inherit;
    }
  }
}

.about__airbnb {
  width: 51.26px;
  height: 16px;

  @media (min-width: $desktop-width) {
    width: 111.76px;
    height: 34.91px;
  }
}

.about__hubspot {
  width: 48px;
  height: 13.71px;

  @media (min-width: $desktop-width) {
    width: 104.65px;
    height: 29.9px;
  }
}

.about__google {
  width: 48px;
  height: 16px;

  @media (min-width: $desktop-width) {
    width: 104.65px;
    height: 34.88px;
  }
}

.about__microsoft {
  width: 69.71px;
  height: 14.86px;

  @media (min-width: $desktop-width) {
    width: 151.99px;
    height: 32.39px;
  }
}

.about__walmart {
  @media (min-width: $desktop-width) {
    width: 138.28px;
    height: 32.39px;
  }
}

.about__fedex {
  @media (min-width: $desktop-width) {
    width: 99.66px;
    height: 29.9px;
  }
}