@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
.html {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  font-style: normal;
  margin: 0 auto;
}

.body {
  width: 100%;
  font-family: "Inter", "Arial", sans-serif;
  color: #18216D;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

.header {
  width: 100%;
  padding: 16px 20px 21px 16px;
  box-sizing: border-box;
}
@media (min-width: 1600px) {
  .header {
    width: 1400px;
    display: flex;
    align-items: center;
    padding: 25px 0 65.76px;
    margin: 0 auto;
  }
}

.header-status {
  display: flex;
  justify-content: space-between;
  padding: 0 0 0 4px;
  margin: 0 0 26px;
}
@media (min-width: 1600px) {
  .header-status {
    display: none;
  }
}

.header-status__time {
  width: 30.76px;
  height: 12px;
}

.header-status__indicator {
  width: 73px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-status__signal {
  width: 17.96px;
  height: 11.29px;
}

.header-status__wifi {
  width: 16.14px;
  height: 11.61px;
}

.header-status__battery {
  width: 25.71px;
  height: 12px;
}

.header-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 8px 0 0;
}
@media (min-width: 1600px) {
  .header-navigation {
    width: 1400px;
    justify-content: flex-start;
    padding: 0;
  }
}

.header-navigation__logo {
  width: 70.4px;
  height: 20px;
}
@media (min-width: 1600px) {
  .header-navigation__logo {
    width: 114px;
    height: 32px;
    margin: 0 110px 0 0;
    cursor: pointer;
  }
}

.header-navigation__list {
  display: none;
}
@media (min-width: 1600px) {
  .header-navigation__list {
    width: 507px;
    font-size: 18px;
    line-height: 20px;
    font-weight: 500;
    list-style: none;
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin: 0 auto 0 0;
  }
}

.header-navigation__item {
  cursor: pointer;
}

.header-navigation__log-in {
  display: none;
}
@media (min-width: 1600px) {
  .header-navigation__log-in {
    width: 239px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 1600px) {
  .header-navigation__log-in-sign {
    font-size: 18px;
    line-height: 20px;
    font-weight: 500;
    cursor: pointer;
  }
}

@media (min-width: 1600px) {
  .header-navigation__log-in-download {
    min-width: 136px;
    font-size: 17px;
    line-height: 20px;
    font-weight: 500;
    border-radius: 6px;
    color: white;
    background: #18216D;
    padding: 16px 27px;
    box-sizing: border-box;
    cursor: pointer;
  }
}

.header-navigation__menu-button {
  width: 28px;
  height: 12.2px;
  border: none;
  background: transparent;
  padding: 0;
  margin: 0;
  cursor: pointer;
}
@media (min-width: 1600px) {
  .header-navigation__menu-button {
    display: none;
  }
}

.preview {
  width: 100%;
  min-height: 564px;
  text-align: center;
  position: relative;
  padding: 20px 12.98px 0 12px;
  box-sizing: border-box;
}
@media (min-width: 1600px) {
  .preview {
    width: 1400px;
    text-align: left;
    padding: 0 0 0 116px;
    margin: 0 auto;
  }
}

.preview__title {
  font-size: 28px;
  line-height: 27px;
  font-weight: 600;
  margin: 0 0 20px;
}
@media (min-width: 1600px) {
  .preview__title {
    width: 498px;
    font-size: 48px;
    line-height: 60px;
    padding: 78.24px 0 0;
    margin: 0 0 17px;
  }
}

.preview__description {
  width: 74.67%;
  min-width: 280px;
  margin: 0 auto 29px;
}
@media (min-width: 1600px) {
  .preview__description {
    width: 484px;
    font-size: 17px;
    line-height: 28px;
    margin: 0 0 34px;
  }
