.preview__button {
  min-width: 120px;
  min-height: 40px;
  font-family: inherit;
  font-size: 14px;
  line-height: 16.94px;
  border: none;
  border-radius: 6px;
  background: #00ECD0;
  padding: 11px 17px 12px;
  margin: 0;
}
@media (min-width: 1600px) {
  .preview__button {
    font-size: 17px;
    line-height: 20.57px;
    font-weight: 500;
    padding: 24px 37px 22px 38px;
