  }
}

.preview__image {
  width: 350.02px;
  height: 300px;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}
@media (min-width: 1600px) {
  .preview__image {
    width: 786px;
    height: 518.35px;
    bottom: 60px;
    left: 910px;
  }
}

.about {
