.header-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0 8px 0 0;

  @media (min-width: $desktop-width) {
    width: 1400px;

    justify-content: flex-start;

    padding: 0;
  }
}

.header-navigation__logo {
  width: 70.4px;
  height: 20px;

  @media (min-width: $desktop-width) {
    width: 114px;
    height: 32px;

    margin: 0 110px 0 0;

    cursor: pointer;
  }
}

.header-navigation__list {
  display: none;

  @media (min-width: $desktop-width) {
    width: 507px;

    font-size: 18px;
    line-height: 20px;
    font-weight: 500;
    list-style: none;

    display: flex;
    justify-content: space-between;

    padding: 0;
    margin: 0 auto 0 0;
  }
}

.header-navigation__item {
  cursor: pointer;
}

.header-navigation__log-in {
  display: none;

  @media (min-width: $desktop-width) {
    width: 239px;

    display: flex;
    justify-content: space-between;
    align-items: center;
    
  }
}

.header-navigation__log-in-sign {
  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 20px;
    font-weight: 500;

    cursor: pointer;
  }
}

.header-navigation__log-in-download {
  @media (min-width: $desktop-width) {
    min-width: 136px;

    font-size: 17px;
    line-height: 20px;
    font-weight: 500;

    border-radius: 6px;

    color: white;
    background: $main-color-text;

    padding: 16px 27px;
    box-sizing: border-box;

    cursor: pointer;
  }
}

.header-navigation__menu-button {
  width: 28px;
  height: 12.2px;

  border: none;

  background: transparent;

  padding: 0;
  margin: 0;

  cursor: pointer;

  @media (min-width: $desktop-width) {
    display: none;
  }
}