.preview {
  width: 100%;
  min-height: 564px;

  text-align: center;

  position: relative;

  padding: 20px 12.98px 0 12px;
  box-sizing: border-box;

  @media (min-width: $desktop-width) {
    width: 1400px;

    text-align: left;

    padding: 0 0 0 116px;
    margin: 0 auto;
  }
}

.preview__title {
  font-size: 28px;
  line-height: 27px;
  font-weight: 600;

  margin: 0 0 20px;

  @media (min-width: $desktop-width) {
    width: 498px;

    font-size: 48px;
    line-height: 60px;

    padding: 78.24px 0 0;
    margin: 0 0 17px;
  }
}

.preview__description {
  width: 74.67%;
  min-width: 280px;

  margin: 0 auto 29px;

  @media (min-width: $desktop-width) {
    width: 484px;

    font-size: 17px;
    line-height: 28px;

    margin: 0 0 34px;
  }
}

.preview__button {
  min-width: 120px;
  min-height: 40px;
  font-family: inherit;
  font-size: 14px;
  line-height: 16.94px;

  border: none;
  border-radius: 6px;

  background: #00ECD0;

  padding: 11px 17px 12px;
  margin: 0;

  @media (min-width: $desktop-width) {
    font-size: 17px;
    line-height: 20.57px;
    font-weight: 500;

    padding: 24px 37px 22px 38px;

    cursor: pointer;
  }
}

.preview__image {
  width: 350.02px;
  height: 300px;

  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);

  @media (min-width: $desktop-width) {
    width: 786px;
    height: 518.35px;

    bottom: 60px;
    left: 910px;
    
  }
}